import React, { useState } from 'react';
import { FieldRenderProps } from 'react-final-form';
import styles from './text-input.module.css';

interface Props extends FieldRenderProps<string, any> {
  label?: string;
  placeholder?: string;
}

export const TextInput = (props: Props) => {

  const { meta, input, label, placeholder } = props
  const [ isShowingPassword, setShowPassword ] = useState(false)

  return (
    <div className={styles.container}>
      <label className={styles.label}>
        {label && (
          <span className={styles.labelText}>
            {label}
          </span>
        )}
        <input
          className={styles.input}
          placeholder={placeholder}
          {...input}
          type={isShowingPassword ? 'text' : input.type}
        />
        {input.type === 'password' && (
          <button
            onClick={() => setShowPassword(!isShowingPassword)}
            className={styles.showButton}
          >
            <i className="material-icons">{isShowingPassword ? 'visibility' : 'visibility_off'}</i>
          </button>
        )}
      </label>
      <div className={styles.error}>
        {meta.error && meta.touched && (
          <span className={styles.errorText}>
            {meta.error}
          </span>
        )}
      </div>
    </div>
  );
}

export default TextInput;
